"use client";

import { useState } from "react";
import { useForm } from "react-hook-form";
import { zodResolver } from "@hookform/resolvers/zod";
import * as z from "zod";
import { Button } from "@/components/ui/button";
import { Form, FormControl, FormField, FormItem, FormLabel, FormMessage } from "@/components/ui/form";
import { Input } from "@/components/ui/input";
import { Textarea } from "@/components/ui/textarea";
import { FileUpload } from "./FileUpload";
import { LayoutWrapper } from "./LayoutWrapper";
import { HeroSection } from "./HeroSection";
import { ArrowLeft, FileText, Plus, Trash2, X } from "lucide-react";
import Link from "next/link";
import { Select, SelectTrigger, SelectValue, SelectContent, SelectItem } from "@/components/ui/select";
import { TooltipField } from "../../2-components/innovation-form/TooltipField";
import { TEAM_ROLES } from "../../2-components/innovation-form/types";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";

// Form validation schema
const formSchema = z.object({
  projectName: z.string().min(2, "Project name must be at least 2 characters"),
  projectDescription: z.string().min(20, "Description must be at least 20 characters"),
  businessModel: z.string().min(10, "Please describe your business model"),
  targetMarket: z.string().min(10, "Please describe your target market"),
  fundingNeeds: z.string().min(5, "Please specify funding needs"),
  teamMembers: z.string().min(5, "Please list key team members"),
  documents: z.instanceof(FileList).optional(),
  terms: z.boolean().refine((val) => val === true, "You must accept the terms"),
});

export function FinalStartup() {
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [newMember, setNewMember] = useState({ name: "", email: "", role: "" });
  const [formData, setFormData] = useState<{
    documents: any;
    teamMembers?: { name: string; email: string; role: string }[];
    generalContact?: { primaryPhone?: string; primaryEmail?: string };
  }>({ teamMembers: [], generalContact: {} });
  const [uploadingFiles] = useState<Set<string>>(new Set());

  const updateFormData = (updatedFields: Partial<typeof formData>) => {
    setFormData((prev) => ({ ...prev, ...updatedFields }));
  };
 const REQUIRED_DOCUMENTS = [
  "Project Proposal / Concept Note",
  "Executive Summary", 
  "Business Model Canvas",
  "Implementation Roadmap / Gantt Chart",
  "Team Profiles / CVs"
];
  const removeRequiredFile = (docType: string) => {
    const { [docType]: _removed, ...rest } = formData.documents.required;
    void _removed;
    updateFormData({
      documents: {
        ...formData.documents,
        required: rest,
      },
    });
  };

  const handleAddTeamMember = () => {
    if (newMember.name.trim() && newMember.email.trim() && newMember.role) {
      updateFormData({
        teamMembers: [...(formData.teamMembers || []), newMember]
      });
      setNewMember({ name: "", email: "", role: "" });
    }
  };

  const handleRemoveTeamMember = (index: number) => {
    const updatedMembers = (formData.teamMembers || []).filter((_, i) => i !== index);
    updateFormData({ teamMembers: updatedMembers });
  };



  const form = useForm<z.infer<typeof formSchema>>({
    resolver: zodResolver(formSchema),
    defaultValues: {
      projectName: "",
      projectDescription: "",
      businessModel: "",
      targetMarket: "",
      fundingNeeds: "",
      teamMembers: "",
      terms: false,
    },
  });

  const onSubmit = async (values: z.infer<typeof formSchema>) => {
    setIsSubmitting(true);
    console.log("Form values:", values);
    
    // Simulate form submission
    await new Promise(resolve => setTimeout(resolve, 1500));
    
    setIsSubmitting(false);
    alert("Project startup form submitted successfully!");
  };

  function handleFileUpload(docType: string, file: File) {
    throw new Error("Function not implemented.");
  }

  function isValidFile(file: File) {
    const allowedExtensions = [".pdf", ".doc", ".docx", ".ppt", ".pptx"];
    const fileName = file.name.toLowerCase();
    return allowedExtensions.some(ext => fileName.endsWith(ext));
  }

  return (
    
    <LayoutWrapper className="py-12 bg-[#E3EAF2] w-full my-4 rounded-lg">
                <div className="relative mb-8">
          {/* Left Icon */}
          <div className="absolute left-0 top-1/2 -translate-y-1/2">
            <Link href="/dashboard/projectPage">
              <Button variant="ghost" size="icon" className="rounded-full">
                <ArrowLeft  className="h-5 w-5" />
              </Button>
            </Link>
          </div>
        
          {/* Centered Text */}
          <div className="text-center">
      <HeroSection
        title="Project Startup Application"
        subtitle="Complete the form to register your new project"
      />
          </div>
        </div>

      <Form {...form}>
        <form onSubmit={form.handleSubmit(onSubmit)} className="space-y-8">
          <div className="grid grid-cols-1 gap-8 md:grid-cols-2">

            {/* Business Details */}
            <div className="space-y-6">

              <h3 className="text-lg font-semibold text-gray-900">Project Details</h3>
              
              <FormField
                control={form.control}
                name="businessModel"
                render={({ field }) => (
                  <FormItem>
                    <FormLabel>Project Model *</FormLabel>
                    <FormControl>
                      <Textarea
                        placeholder="Describe your revenue model"
                        className="min-h-[100px]"
                        {...field}
                      />
                    </FormControl>
                    <FormMessage />
                  </FormItem>
                )}
              />

              <FormField
                control={form.control}
                name="targetMarket"
                render={({ field }) => (
                  <FormItem>
                    <FormLabel>Target Market *</FormLabel>
                    <FormControl>
                      <Textarea
                        placeholder="Who are your target customers?"
                        className="min-h-[100px]"
                        {...field}
                      />
                    </FormControl>
                    <FormMessage />
                  </FormItem>
                )}
              />
            </div>

        <div className="space-y-4">
          <TooltipField
            label="Team Members"
            tooltip="Add all team members involved in this project with their roles"
            required
          >
            <div className="space-y-3">
              {(formData.teamMembers || []).map((member, index) => (
                <div key={index} className="flex flex-col sm:flex-row gap-2 p-3 border rounded-lg bg-muted/30">
                  <div className="flex-1 space-y-2 sm:space-y-0 sm:space-x-2 sm:flex sm:items-center">
                    <Input value={member.name} readOnly className="flex-1" placeholder="Name" />
                    <Input value={member.email} readOnly className="flex-1" placeholder="Email" />
                    <div className="w-full sm:w-32">
                      <span className="inline-block px-2 py-1 text-xs bg-primary/10 text-primary rounded">
                        {member.role}
                      </span>
                    </div>
                  </div>
                  <Button
                    type="button"
                    variant="outline"
                    size="sm"
                    onClick={() => handleRemoveTeamMember(index)}
                    className="self-start sm:self-center"
                  >
                    <X className="w-4 h-4" />
                  </Button>
                </div>
              ))}
              
              {/* Add new member form */}
              <div className="flex flex-col sm:flex-row gap-2 p-3 border-2 border-dashed border-muted-foreground/30 rounded-lg">
                <Input
                  value={newMember.name}
                  onChange={(e) => setNewMember({ ...newMember, name: e.target.value })}
                  placeholder="Member name"
                  className="flex-1"
                />
                <Input
                  type="email"
                  value={newMember.email}
                  onChange={(e) => setNewMember({ ...newMember, email: e.target.value })}
                  placeholder="Email address"
                  className="flex-1"
                />
                <Select
                  value={newMember.role}
                  onValueChange={(value) => setNewMember({ ...newMember, role: value })}
                >
                  <SelectTrigger className="w-full sm:w-32">
                    <SelectValue placeholder="Role" />
                  </SelectTrigger>
                  <SelectContent>
                    {TEAM_ROLES.map((role) => (
                      <SelectItem key={role} value={role}>
                        {role}
                      </SelectItem>
                    ))}
                  </SelectContent>
                </Select>
                <Button
                  type="button"
                  variant="outline"
                  size="sm"
                  onClick={handleAddTeamMember}
                  disabled={!newMember.name.trim() || !newMember.email.trim() || !newMember.role}
                  className="w-full sm:w-auto"
                >
                  <Plus className="w-4 h-4" />
                </Button>
              </div>
            </div>
          </TooltipField>
                {/* Required Documents */}
                <Card>
                  <CardHeader>
                    <CardTitle className="text-lg text-foreground flex items-center gap-2">
                      <FileText className="h-5 w-5" />
                      Required Documents (5/5)
                    </CardTitle>
                  </CardHeader>
                  <CardContent className="space-y-4">
                    {REQUIRED_DOCUMENTS.map((docType) => (
                      <div key={docType} className="border border-border rounded-lg p-4">
                        <TooltipField
                          label={docType}
                          tooltip={`Upload your ${docType.toLowerCase()} document. Accepted formats: PDF, DOCX, PPT`}
                          id={`required-${docType}`}
                        />
                        
                        {formData.documents.required[docType] ? (
                          <div className="mt-2 flex items-center justify-between bg-muted/50 p-3 rounded">
                            <div className="flex items-center gap-2">
                              <FileText className="h-4 w-4 text-primary" />
                              <span className="text-sm font-medium">{formData.documents.required[docType]}</span>
                            </div>
                            <Button
                              variant="ghost"
                              size="sm"
                              onClick={() => removeRequiredFile(docType)}
                              className="text-destructive hover:text-destructive"
                            >
                              <Trash2 className="h-4 w-4" />
                            </Button>
                          </div>
                        ) : (
                          <div className="mt-2">
                            <Input
                              type="file"
                              accept=".pdf,.docx,.doc,.pptx,.ppt"
                              onChange={(e) => {
                                const file = e.target.files?.[0];
                                if (file) {
                                  if (isValidFile(file)) {
                                    handleFileUpload(docType, file);
                                  } else {
                                    alert("Please upload a PDF, DOCX, or PPT file");
                                  }
                                }
                              }}
                              disabled={uploadingFiles.has(docType)}
                              className="cursor-pointer"
                            />
                            {uploadingFiles.has(docType) && (
                              <div className="mt-2 flex items-center gap-2 text-sm text-muted-foreground">
                                <div className="animate-spin rounded-full h-4 w-4 border-b-2 border-primary"></div>
                                Uploading...
                              </div>
                            )}
                          </div>
                        )}
                      </div>
                    ))}
                  </CardContent>
                </Card>
        </div>

            {/* Documents Upload */}
            <div className="space-y-6 md:col-span-2">
              <h3 className="text-lg font-semibold text-gray-900">Supporting Documents</h3>
              
              <FormField
                control={form.control}
                name="documents"
                render={({ field }) => (
                  <FormItem>
                    <FormLabel>Upload Documents (PDF, DOC, PPT, XLS)</FormLabel>
                    <FormControl>
                      <FileUpload
                        accept=".pdf,.doc,.docx,.ppt,.pptx,.xls,.xlsx"
                        onChange={field.onChange}
                      />
                    </FormControl>
                    <FormMessage />
                  </FormItem>
                )}
              />
            </div>

            {/* Terms Agreement */}
            <div className="md:col-span-2">
              <FormField
                control={form.control}
                name="terms"
                render={({ field }) => (
                  <FormItem className="flex items-start space-x-3 space-y-0">
                    <FormControl>
                      <input
                        type="checkbox"
                        placeholder=" value"
                        className="h-4 w-4 mt-1 rounded border-gray-300 text-primary focus:ring-primary"
                        checked={field.value}
                        onChange={(e) => field.onChange(e.target.checked)}
                      />
                    </FormControl>
                    <div className="space-y-1 leading-none">
                      <FormLabel>
                        I agree to the terms and conditions *
                      </FormLabel>
                      <p className="text-sm text-gray-500">
                        By submitting this form, you confirm that all information provided is accurate.
                      </p>
                    </div>
                    <FormMessage />
                  </FormItem>
                )}
              />
            </div>
          </div>

          <div className="flex justify-end">
            <Button type="submit" size="lg" disabled={isSubmitting}>
              {isSubmitting ? "Submitting..." : "Submit Application"}
            </Button>
          </div>
        </form>
      </Form>
    </LayoutWrapper>
  );
}


